body {
  font-family: Arial, Helvetica, sans-serif;
}

/* Theme transition styles */
:root {
  --theme-transition-duration: 0.3s;
}

body {
  transition: background-color var(--theme-transition-duration) ease,
    color var(--theme-transition-duration) ease;
}

* {
  transition-property: background-color, border-color, color, fill, stroke;
  transition-timing-function: ease;
  transition-duration: var(--theme-transition-duration);
}

/* Exclude certain elements from transition */
.no-theme-transition,
.no-theme-transition * {
  transition: none !important;
}

/* Smooth transition for chat bubbles */
.bg-muted\/50,
.bg-primary\/10,
.bg-primary\/20 {
  transition: background-color var(--theme-transition-duration) ease;
}

/* Smooth transition for icons */
.text-primary,
.text-muted-foreground {
  transition: color var(--theme-transition-duration) ease;
}

/* Smooth transition for the theme toggle button */
.rotate-0,
.rotate-90,
.-rotate-90,
.scale-0,
.scale-100 {
  transition: all var(--theme-transition-duration) ease;
}

/* Header blur and background transitions */
.backdrop-blur-md {
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
}

/* Smooth scroll behavior */
html {
  scroll-behavior: smooth;
}

/* Improved header transitions */
header {
  transition: background-color 0.3s ease, backdrop-filter 0.3s ease;
}
